.rm-form {
    $rm-form: &;
    width: 100%;

    &__content {
        $content: &;
        padding: 24px 0;
        background-color: $color-yellow;
        border-radius: 8px;

        #{$rm-form}--white & {
            background-color: transparent;
            border: 2px solid $color-grey-light;
            padding: 24px 0;
            @include respond-to(md) {
                padding: 16px 0px;
            }
        }

        @include respond-to(md) {
            padding: 16px 0;
        }

        &-header {
            padding: 0 24px;
            display: flex;
            justify-content: space-between;

            @include respond-to(md) {
                padding: 0 12px;
                flex-direction: column;
                align-items: flex-start;
                justify-content: flex-start;;
            }

            &--vertical {
                flex-direction: column;
                align-items: flex-start;
                justify-content: flex-start;

                #{$content}-title {
                    display: block;
                }
            }

            #{$content}-title + #{$content}-more {
                @include respond-to(md) {
                    margin-top: 8px;
                }
            }

            #{$content}-title + #{$content}-subtitle {
                margin-top: 8px;
            }
        }

        &-title {
            @include font-size-get('h2');
            font-weight: 700;
            display: flex;
            align-items: center;
            flex: 1 0 auto;

            & > .link {
                border-bottom-width: 2px;
            }

            & > #{$content}-result {
                margin-left: 16px;
            }

            @include respond-to(md) {
                flex-direction: column;
                align-items: initial;
                justify-content: flex-start;
                width: 100%;

                & > #{$content}-result {
                    margin-left: 0;
                    margin-top: 8px;
                }
            }

            #{$content}-header--vertical & {
                align-items: flex-start;
            }

            &--fluid {
                padding: 0;
                border: none;
                background-color: transparent;
            }

        }

        &-result {

        }

        &-more {
            @include font-size-get('p1');
            display: flex;
            align-items: center;
        }

        &-main {
            padding: 0 24px;

            @include respond-to(md) {
                padding: 0 12px;
            }
        }

        &-header + &-main {
            margin-top: 15px;
            padding-top: 15px;
            border-top: 2px solid $color-yellow-dark;

            #{$rm-form}--white & {
                border-top: 2px solid $color-grey-light;
            }
        }

        &-fluid {
            padding: 0;
            border: none;
            background-color: transparent;
        }
    }

    &__footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include respond-to(md) {
            flex-direction: column;
            justify-content: left;
            align-items: normal;
        }
        &-count {
            @include respond-to(md) {
                order:1;
            }
        }
        &-count + &-reset {
            @include respond-to(md) {
                margin-bottom: 16px;
            }
        }
        &-reset {
            @include respond-to(md) {
                order:0;
            }
        }
    }

    &__content + &__footer {
        margin-top: 16px;
    }

    &__field {
        &-label {

        }

        &-options {
            &--submit {
                display: flex;
                align-items: center;

                @include respond-to(md) {
                    flex-direction: column;
                    align-items: initial;
                }
            }

            &--vertical {
                flex-direction: column;
                align-items: flex-start;

            }
        }

        &-label + &-options {
            margin-top: 8px;
        }
    }

    &__option {
        &--submit {
            &-text {
                @include font-size-get('p2');
                color: $color-grey-dark;
            }

            &-button + &-text {
                margin-left: 16px;

                @include respond-to(md) {
                    margin-top: 8px;
                    margin-left: 0;
                }
            }

        }

        & + &--vertical {
            margin-top: 8px;
            margin-left: 0;
        }
    }

    &--fluid {
        #{$rm-form} {
            &__content {
                padding: 0;
                background-color: transparent;
                border-radius: 0;
            }
        }
    }
}
