// File for scss-vars.
// For example: main colors, paddings and so on.

$font-family-base: 'Manrope', sans-serif;

//Colors
//Main
$color-red: #FF6666;
$color-yellow: #FFDD66;
$color-green: #6CE4AA;
$color-black: #282828;
$color-white: #FFFFFF;

$color-rgba-white-70: rgba(255, 255, 255, .70);
$color-rgba-white-90: rgba(255, 255, 255, .90);
$color-rgba-white-95: rgba(255, 255, 255, .95);

//Additional
$color-yellow-dark: #E5C03C;
$color-yellow-light: #FFEBA4;
$color-green-light: #AEF6D4;
$color-grey-dark: #737373;
$color-grey-medium: #CDCDCD;
$color-gray: #CDCDCD;
$color-grey-light: #F2F2F2;
//Background-color
$color-bg-sand: #FEFAEF;
$color-bg-grape: #F7F3FE;
$color-bg-blue-light: #EEF9FF;
$color-bg-green-light: #EFFEF7;
$color-bg-red-light: #FFF7F7;

/// Breakpoints map
/// @prop {String} keys - Keys are identifiers mapped to a given length
/// @prop {Map} values - Values are actual breakpoints expressed in pixels
$breakpoints: (
    xs: 0,
    md: 768px,
    lg: 1280px,
    xl: 1440px
) !default;

// Breakpoint map for bootstrap
$grid-breakpoints: (
    xs: 0,
    md: 768px,
    lg: 1280px,
    xl: 1440px
) !default;

// Container max-widths for bootstrap
$container-max-widths: (
   /* sm: 100%,*/
    md: 720px,
    lg: 1152px,
    xl: 1344px
) !default;

//Table of responsible fonts
//
//Define the font-sizes and line-height or ratio (ratio column)
//More: https://designpub.ru/%D1%88%D1%80%D0%B8%D1%84%D1%82%D1%8B-%D0%B2-%D0%B0%D0%B4%D0%B0%D0%BF%D1%82%D0%B8%D0%B2%D0%BD%D0%BE%D0%BC-%D0%B4%D0%B8%D0%B7%D0%B0%D0%B9%D0%BD%D0%B5-%D0%BA%D0%B0%D0%BA-%D0%BE%D1%80%D0%B3%D0%B0%D0%BD%D0%B8%D0%B7%D0%BE%D0%B2%D0%B0%D1%82%D1%8C-%D1%80%D0%B0%D0%B1%D0%BE%D1%82%D1%83-ff4fd0d1577d

$font-sizes: (
    //   font-size,      (line-height)
    //   xs, md, lg, xl
    h1: (22, 24, 32, 32, (30px, 32px, 44px, 44px)),
    h2: (18, 18, 20, 20, (24px, 24px, 28px, 28px)),
    h3: (12, 12, 14, 14, (18px, 18px, 20px, 20px)),
    p1: (12, 12, 14, 14, (18px, 18px, 20px, 20px)),
    p2: (10, 10, 12, 12, (14px, 14px, 16px, 16px)),
    button: (12, 12, 14, 14, (18px, 18px, 20px, 20px)),
) !default;
