.banner-home {
    height: calc(100vh - 84px);
    position: relative;
    overflow: hidden;

    @include respond-to(lg) {
        height: calc(100vh - 66px);
    }

    @include respond-to(md) {
        height: 450px;
    }

    &__slider {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: -1;

        &-item {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
            z-index: 1;
            transition: transform ease-in-out 500ms;
            will-change: transform;
            overflow: hidden;

            &-bg {
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                background-size: cover;
                background-position: center;
                animation: image-scale 40s infinite ease;
            }

            &.next {
                opacity: 1;
                animation-duration: 500ms;
                animation-fill-mode: forwards;
                animation-name: slider-item-next;
            }

            &.next-reverse {
                opacity: 1;
                animation-duration: 500ms;
                animation-fill-mode: forwards;
                animation-name: slider-item-next-reverse;
            }

            &.active {
                opacity: 1;
                z-index: 2;

                &.active-next {
                    animation-duration: 500ms;
                    animation-fill-mode: forwards;
                    animation-name: slider-item-active-next;
                }

                &.active-next-reverse {
                    animation-duration: 500ms;
                    animation-fill-mode: forwards;
                    animation-name: slider-item-active-next-reverse;
                }
            }
        }

        @keyframes slider-item-next {
            0% {
                transform: translateX(100%);
            }
            100% {
                transform: translateX(0);
            }
        }

        @keyframes slider-item-active-next {
            0% {
                transform: translateX(0);
            }
            100% {
                transform: translateX(-100%);
            }
        }

        @keyframes slider-item-next-reverse {
            0% {
                transform: translateX(-100%);
            }
            100% {
                transform: translateX(0);
            }
        }

        @keyframes slider-item-active-next-reverse {
            0% {
                transform: translateX(0);
            }
            100% {
                transform: translateX(100%);
            }
        }

        @-webkit-keyframes image-scale {
            0% {
                transform: scale(1.0);
                -ms-transform: scale(1.0);
                /* IE 9 */

                -webkit-transform: scale(1.0);
                /* Safari and Chrome */

                -o-transform: scale(1.0);
                /* Opera */

                -moz-transform: scale(1.0);
                /* Firefox */
            }
            50% {
                transform: scale(1.05);
                -ms-transform: scale(1.05);
                /* IE 9 */

                -webkit-transform: scale(1.05);
                /* Safari and Chrome */

                -o-transform: scale(1.05);
                /* Opera */

                -moz-transform: scale(1.05);
                /* Firefox */
            }
            100% {
                transform: scale(1.0);
                -ms-transform: scale(1.0);
                /* IE 9 */

                -webkit-transform: scale(1.0);
                /* Safari and Chrome */

                -o-transform: scale(1.0);
                /* Opera */

                -moz-transform: scale(1.0);
                /* Firefox */
            }
        }
    }

    &__inner {
        height: 100%;
        width: 100%;
        padding: 80px 0;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-start;

        @include respond-to(md) {
            padding: 140px 0 32px;
        }
    }

    &__content {
        $content: &;
        padding: 24px 32px 24px;
        background-color: $color-white;
        border-radius: 8px;
        display: flex;
        flex-direction: column;
        overflow: hidden;
        position: relative;
        will-change: overflow, border-radius;
        -webkit-mask-image: -webkit-radial-gradient(white, black); // магия не иначе. Для border-radius на safari
        order: 1;

        @include respond-to(md) {
            width: 100%;
            order: 2;
        }

        &--with-progress {
            padding: 24px 32px 8px;
        }

        &-text {
            position: relative;
            overflow: hidden;
            width: 650px;
            height: 0;
            transition: height ease-in-out 300ms;
            will-change: height;

            @include respond-to(lg) {
                width: 450px;
                height: 64px;
            }

            @include respond-to(md) {
                width: 100%;
                height: 90px;
            }

            &-item {
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                display: flex;
                @include font-size-get('h1');
                font-weight: 700;
                z-index: 1;
                text-decoration: none;
                color: $color-black;
                opacity: 0;
                pointer-events: none;
                transition: all ease-in-out 300ms;
                will-change: opacity, transform;

                &.next {
                    opacity: 1;
                    animation-duration: 300ms;
                    animation-fill-mode: forwards;
                    animation-name: text-item-next;
                }

                &.next-reverse {
                    opacity: 1;
                    animation-duration: 300ms;
                    animation-fill-mode: forwards;
                    animation-name: text-item-next;
                }

                &.active {
                    opacity: 1;
                    pointer-events: all;

                    &.active-next {
                        opacity: 0;
                        pointer-events: none;
                        animation-duration: 300ms;
                        animation-fill-mode: forwards;
                        animation-name: text-item-active-next;
                    }

                    &.active-next-reverse {
                        opacity: 0;
                        pointer-events: none;
                        animation-duration: 300ms;
                        animation-fill-mode: forwards;
                        animation-name: text-item-active-next;
                    }
                }

                @keyframes text-item-next {
                    0% {
                        transform: translateY(100%);
                    }
                    100% {
                        transform: translateY(0);
                    }
                }

                @keyframes text-item-active-next {
                    0% {
                        transform: translateY(0);
                    }
                    100% {
                        transform: translateY(100%);
                    }
                }
            }
        }

        &-text + &-action {
            margin-top: 8px;
        }

        &-action {
            @include respond-to(md) {
                display: flex;
                justify-content: flex-start;

                & .button {
                    width: auto;
                }
            }
        }

        &:after {
            content: '';
            width: 612px;
            height: 612px;
            position: absolute;
            background-color: $color-yellow;
            border-radius: 50%;
            z-index: 0;
            top: -306px;
            left: 360px;
            transition: all ease-in-out 500ms;
            will-change: left, top;

            @include respond-to(md) {
                left: 250px;
                top: -400px
            }
        }

        &.decor-reverse:after {
            top: -530px;
            left: -100px;

            @include respond-to(lg){
                top: -535px;
                left: -120px;
            }

            @include respond-to(md){
                top: -535px;
                left: -150px;
            }
        }

        &-progress {
            margin-top: 28px;
            width: 100%;
            max-width: 200px;
            height: 4px;
            background-color: $color-grey-light;
            position: relative;
            border-radius: 2px;
            overflow: hidden;
            display: none;

            &.show {
                display: flex;
            }

            &-complete {
                position: absolute;
                top: 0;
                left: 0;
                height: 4px;
                border-radius: 2px;
                background-color: $color-yellow;
                transition: width linear 250ms;
            }
        }
    }

    &__nav {
        padding: 0 32px;
        display: none;
        order: 2;

        @include respond-to(md) {
            order: 1;
        }

        &.show {
            display: flex;
        }

        &-prev, &-next {
            display: flex;
            transition: opacity ease-in-out 300ms;
            cursor: pointer;
            opacity: .5;
            will-change: opacity;

            &-svg {
                fill: $color-white;
            }

            &:hover {
                opacity: 1;
            }
        }

        &-next {
            transform: rotate(180deg);
        }

        &-prev + &-next {
            margin-left: 8px;
        }
    }

    &__content + &__nav {
        @include respond-from(md) {
            margin-top: 8px;
        }

        @include respond-to(md) {
            margin-bottom: 16px;
        }
    }
}
