.footer {
    padding: 32px 0;
    background-color: $color-grey-light;

    &__form {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        padding-bottom: 32px;

        @include respond-to(lg) {
            padding-bottom: 24px;
        }

        &-title {
            @include font-size-get('h2');
            font-weight: 700;
        }

        &-title + &-button {
            margin-left: 24px;

            @include respond-to(lg) {
                margin-left: 16px;
            }

            @include respond-to(md) {
                margin-left: 0;
                margin-top: 16px;
                width: 100%;
            }
        }
    }

    &__menu {
        margin-bottom: 24px;
    }

    &__form + &__menu, &__menu + &__copyright, &__form + &__copyright {
        padding-top: 24px;
        border-top: 1px solid $color-grey-medium;
    }

    &__logo {
        display: flex;
        height: 100%;
        width: 100%;
        justify-content: flex-end;
        align-items: flex-end;
        text-decoration: none;
        color: $color-black;

        @include respond-to(lg) {
            margin-top: 24px;
            justify-content: flex-start;
            align-items: flex-start;
        }
    }
}
