.typography {
    h1 + *, * + h1,
    ul + *, * + ul,
    h2 + *, * + h2,
    ol + *, * + ol,
    h3 + *, * + h3,
    h4 + *, * + h4,
    h5 + *, * + h5,
    p + *, * + p {
        margin-top: 16px;
    }
    h1 + &__concat-small,
    ul + &__concat-small,
    h2 + &__concat-small,
    ol + &__concat-small,
    h3 + &__concat-small,
    h4 + &__concat-small,
    h5 + &__concat-small,
    p + &__concat-small {
        margin-top: 8px;
    }
    ol {
        margin: 0;
        padding: 0;
        list-style: none;
        counter-reset: num;
        position: relative;

        & > li {

            &:before {
                left: 0;
                content: counter(num) '. ';
                counter-increment: num;
            }

            & + li {
                margin-top: 16px;
            }

            & > ul {

            }
        }

    }

    ul {
        margin: 0;
        padding: 0;
        list-style: none;

        li {
            position: relative;

            &:before {
                content: '';


                left: 0;


                width: 6px;
                height: 6px;

                border-radius: 50%;

            }

            & + li {
                margin-top: 16px;
            }
        }
    }

    &--color {
        &-white {
            ul {
                li {
                    &:before {

                    }
                }
            }
        }
    }
}
