.menu-bottom {
    width: 100%;
    position: relative;

    &__inner {
        width: 100%;
        column-count: 4;

        @include respond-to(lg) {
            column-count: 3;
        }

        @include respond-to(md) {
            column-count: 1;
        }
    }

    &__item {
        $item: &;
        display: flex;
        flex-direction: column;
        page-break-inside: avoid;
        break-inside: avoid;
        align-items: flex-start;
        padding: 8px 0 6px;

        &--phone, &--email {
            font-weight: 700;
        }

        &-title {
            text-decoration: none;
            color: $color-black;
            @include font-size-get('h3');
            font-weight: 700;
            padding-bottom: 1px;
            border-bottom: 1px solid transparent;
            transition: border ease-in-out 150ms;

            &:hover {
                border-color: $color-yellow;
            }

            &--tag {
                position: relative;

                #{$item}-tag {
                    position: absolute;
                    left: calc(100% + 8px);
                    top: 50%;
                    margin-top: -16px;

                    @include respond-to(lg) {
                        margin-top: -15px;
                    }
                }

                // костыль, ага)
                &:hover .tag {
                    background-color: $color-yellow;
                }
            }

            &--marker {
                &-online {
                    font-weight: 400;
                    position: relative;

                    &:after {
                        content: '';
                        position: absolute;
                        right: -16px;
                        top: 50%;
                        margin-top: -4px;
                        width: 8px;
                        height: 8px;
                        border-radius: 4px;
                        background-color: $color-green;
                    }
                }
            }
        }

        &-child {
            text-decoration: none;
            color: $color-black;
            padding-bottom: 1px;
            border-bottom: 1px solid transparent;
            transition: border ease-in-out 150ms;

            @include respond-to(lg) {
                display: none;
            }

            &:hover {
                border-color: $color-yellow;
            }

            & + & {
                margin-top: 14px;
            }

            &--tag {
                position: relative;

                #{$item}-tag {
                    position: absolute;
                    left: calc(100% + 8px);
                    top: 50%;
                    margin-top: -16px;

                    @include respond-to(lg) {
                        margin-top: -15px;
                    }
                }

                // костыль, ага)
                &:hover .tag {
                    background-color: $color-yellow;
                }
            }
        }

        &-title + &-child {
            margin-top: 16px;
        }
    }

    &__social-mobile {
        margin-top: 8px;
    }
}
