        background-position: center;
        background-repeat: no-repeat;
        width: 60px;
        height: 68px;
        position: relative;
        z-index: 100;

        &-label {
            opacity: 0;
            position: absolute;
            transition: all ease-in-out 150ms;
            top: 10px;
            left: 100%;
            width: max-content;
            @include font-size-get('p1');
            color: $color-black;
            background-color: $color-white;
            border-radius: 16px;
            padding: 6px 12px;
        }
        &.active {
            #{$marker} {
                &-label {
                    opacity: 1;
                }
            }
        }
    }
}
