.favourite {
    $favourite: &;

    &--type {
        &-main {
            display: flex;
            align-items: center;
            cursor: pointer;

            #{$favourite}__svg {
                fill: transparent;
                stroke: $color-grey-medium;
                stroke-width: 2px;
                transition: all 150ms ease-in-out;

                &:hover {
                    stroke: $color-red;
                }
            }

            &.active {

                #{$favourite} {
                    &__svg {
                        fill: $color-red;
                        stroke: $color-red;
                    }
                }

                &:hover {
                    #{$favourite} {
                        &__svg {
                            fill: $color-red;
                            stroke: $color-red;
                        }
                    }
                }

            }
        }


        &-secondary {
            display: flex;
            align-items: center;
            cursor: pointer;
            position: relative;

            #{$favourite}__svg {
                fill: transparent;
                stroke: $color-black;
                stroke-width: 2px;
                transition: all 150ms ease-in-out;

                @include respond-to(lg) {
                    max-width: 16px;
                    max-height: 16px;
                }

                &:hover {
                    stroke: $color-red;
                }
            }

            &:after {
                content: '';
                width: 14px;
                height: 14px;
                border-radius: 7px;
                border: 3px solid $color-white;
                background-color: $color-red;
                position: absolute;
                top: 4px;
                right: -3px;
                opacity: 0;
                pointer-events: none;
                transition: all ease-in-out 150ms;

                @include respond-to(lg) {
                    width: 10px;
                    height: 10px;
                    top: 3px;
                    right: -2px;
                }
            }

            &.active {
                &:after {
                    opacity: 1;
                }
            }
        }

    }

    &--color {
        &-dark-grey {
            #{$favourite} {
                &__svg {
                    stroke: $color-grey-dark;
                }
            }
        }
    }

}
