.manager-card {
    display: flex;
    flex-direction: column;
    @include respond-to(lg) {
        flex-direction: row;
    }
    &__image {
        height: 200px;
        width: 200px;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        border-radius: 8px;
        @include respond-to(xl) {
            width: 168px;
            height: 168px;
        }
        @include respond-to(lg) {
            width: 80px;
            height: 80px;
        }

    }

    &__image + &__description {
        margin-top: 16px;
        @include respond-to(lg) {
            margin-top: 0;
            margin-left: 16px;
        }
    }

    &__title {
        @include font-size-get('p1');
        color: $color-black;
    }
    &__title + &__phones {
        margin-top: 16px;
        @include respond-to(lg) {
            margin-top: 8px;
        }
    }
    &__phones {
        &-item {
            @include font-size-get('h3');
            color: $color-black;
            font-weight: bold;
            & + & {
                margin-top: 16px;
                @include respond-to(lg) {
                    margin-top: 8px;
                }
            }
        }
    }
    &__phones + &__emails,
    &__title + &__emails {
        margin-top: 16px;
        @include respond-to(lg) {
            margin-top: 8px;
        }
    }
    &__emails {
        &-item {
            @include font-size-get('h3');
            color: $color-black;
            font-weight: bold;
            & + & {
                margin-top: 16px;
                @include respond-to(lg) {
                    margin-top: 8px;
                }
            }
        }
    }
}
