.bombapomadok {
    $bombapomadok: &;
    padding: 40px 0 32px;

    @include respond-to(lg) {
        padding: 32px 0 32px;
    }

    &--hide {
        display: none;
    }

    &--banner {
        padding: 0 0 32px;

        @include respond-to(lg) {
            padding: 0 0 32px;
        }
    }

    &__banner {
        width: 100%;
        height: 66vh;
        background-position: center;
        background-size: cover;
        @include respond-to(lg) {
            height: 50vh;
        }
    }

    &__breadcrumbs {
        &-item {
            text-decoration: none;
            color: $color-grey-dark;
            cursor: pointer;
            @include font-size-get('p2');
        }
    }

    &__title {

    }

    &__banner + &__breadcrumbs, &__banner + &__title {
        margin-top: 40px;
    }

    &__breadcrumbs + &__title {
        margin-top: 8px;
    }
}
