.link {
    text-decoration: none;
    color: $color-black;
    cursor: pointer;
    /*    padding-bottom: 1px;*/

    display: inline-flex;

    $link: &;
    &__text {
        position: relative;
        &-span {
            transition: all ease-in-out 150ms;
            border-bottom: 1px solid;
            border-color: transparent;
        }
        &-marker {
            display: inline-block;
            width: 8px;
            height: 8px;
            background-color: $color-green;
            border-radius: 100%;
            top: -2px;
            position: relative;
            margin-left: 4px;
            @include respond-to(xl) {
                top: -1px;
            }
        }
    }
    &--size {
        &-h1 {
            #{$link} {
                &__text {
                    &-span {
                        @include font-size-get('h1');
                        font-weight: 700;
                        padding-bottom: 2px;
                        border-bottom: 2px solid transparent;
                    }
                }
            }
        }

        &-h2 {
            #{$link} {
                &__text {
                    &-span {
                        @include font-size-get('h2');
                        font-weight: 700;
                        padding-bottom: 2px;
                        border-bottom: 2px solid transparent;
                    }
                }
            }
        }

        &-h3 {
            #{$link} {
                &__text {
                    &-span {
                        @include font-size-get('h3');
                        font-weight: 700;
                        border-bottom: 1px solid transparent;
                    }
                }
            }
        }

        &-p1 {
            #{$link} {
                &__text {
                    &-span {
                        @include font-size-get('p1');
                        font-weight: 400;
                        border-bottom: 1px solid transparent;
                    }
                }
            }
        }

        &-p2 {
            #{$link} {
                &__text {
                    &-span {
                        @include font-size-get('p2');
                        font-weight: 400;
                        border-bottom: 1px solid transparent;
                    }
                }
            }
        }
    }


    &--border {
        &-hover {
            #{$link} {
                &__text {
                    &-span {
                        border-color: transparent;
                        &:hover {
                            border-color: $color-yellow;
                        }
                    }
                }
            }
        }

        &-static {
            #{$link} {
                &__text {
                    &-span {
                        border-color: $color-yellow;

                    }
                }
            }

        }
    }

    &--dot {
        position: relative;
        margin-left: 16px;

        &:before {
            content: '';
            width: 8px;
            height: 8px;
            position: absolute;
            left: -16px;
            top: 50%;
            margin-top: -4px;
            border-radius: 4px;
        }

        &-green {
            &:before {
                background-color: $color-green;
            }
        }
    }

    &--wrapper {
        display: block;
        width: 100%;
        height: 100%;
        color: inherit;
        border: none;
        padding: 0;
        margin: 0;
        transition: none;
    }

    &--type {
        &-arrow {
            display: flex;
            justify-content: space-between;
            padding: 16px;
            transition: all ease-in-out 150ms;
            border-radius: 4px;

            #{$link} {
                &__text {
                    @include font-size-get('h2');
                    color: $color-black;
                    font-weight: bold;
                }

                &__arrow {
                    @include font-size-get('h2');
                    color: $color-black;
                    font-weight: bold;
                    overflow: hidden;

                    &-inner {
                        transform: translateX(-150%);
                        transition: all ease-in-out 150ms;
                    }
                }
            }

            &:hover {
                background-color: $color-yellow-light;

                #{$link} {
                    &__arrow {
                        &-inner {
                            transform: translateX(0);
                        }
                    }
                }
            }
        }

        &-doc {
            display: flex;
            align-items: center;
            padding: 0;
            border: none;

            #{$link} {
                &__text {
                    color: $color-black;
                    transition: all ease-in-out 150ms;
                    cursor: pointer;

                    &-span {
                        padding-bottom: 1px;
                        border-bottom: 1px solid;
                        border-color: transparent;
                        transition: all ease-in-out 150ms;
                        &:hover {
                            border-color: $color-yellow;
                        }
                    }

                    @include respond-to(md) {
                        order: 1;
                    }
                }

                &__text + #{$link}__expansion {
                    margin-left: 16px;
                    @include respond-to(lg) {
                        margin-left: 8px;
                    }
                    @include respond-to(md) {
                        margin-left: 0;
                    }
                }

                &__expansion {
                    @include font-size-get('p2');
                    color: $color-grey-dark;
                    @include respond-to(md) {
                        width: 50px;
                        order: 0;
                        align-self: flex-start;
                        flex: 0 0 auto;
                    }
                }
            }
        }
    }
}
