.notification {
    $notification: &;
    position: fixed;
    bottom: -300px;
    left: 0;
    width: 100%;
    padding: 24px 0;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: all ease-in-out 300ms;

    @include respond-to(lg) {
        padding: 16px 0;
    }

    &.show {
        bottom: 0;
        visibility: visible;
        opacity: 1;
        pointer-events: all;
    }

    &--cookies {
        background-color: $color-yellow-light;
    }

    &--warning {
        background-color: $color-red;
        color: $color-white;
    }

    &--success {
        background-color: $color-green;
    }

    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;

        #{$notification}--cookies & {
            @include respond-to(md) {
                flex-direction: column;
            }
        }
    }

    &__message {
        display: flex;
        align-items: center;

        &-icon + &-text {
            margin-left: 8px;
        }
    }

    &__action {
        #{$notification}--cookies & {
            @include respond-to(md) {
                width: 100%;
            }
        }

        &-item {
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: flex-end;

            &-svg {
                #{$notification}--warning & {
                    fill: $color-white;
                }
            }
        }
    }

    &__message + &__action {
        margin-left: 40px;

        #{$notification}--cookies & {
            @include respond-to(md) {
                margin-left: 0;
                margin-top: 16px;
            }
        }
    }
}
