.site-preloader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: $color-white;
    z-index: 9999;
    opacity: 1 !important; // important для того, чтобы плавно появился сразу после прогрузки стилей css

    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 40px;

    @include respond-to(lg) {
        padding: 15px 40px 15px;
    }

    @include respond-to(md) {
        padding: 40px 40px 120px;
    }

    &.hide {
        opacity: 0 !important;
        pointer-events: none;
        transform: translateY(-20px) scale(1.05);
    }

    &__content {
        display: flex;
        flex: 1 0 auto;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        &-img + &-text {
            margin-top: 25px;

            @include respond-to(md) {
                margin-top: 15px;
            }
        }
    }

    &__image {
        display: flex;
        justify-content: center;
    }
}