.links {
    $links: &;

    &--type {
        &-sections {
            #{$links} {
                &__section {
                    &-title {
                        @include font-size-get('h2');
                        font-weight: bold;
                        color: $color-black;
                    }
                    &-title + #{$links}__section-list {
                        margin-top: 16px;
                    }
                    & + #{$links}__section {
                        margin-top: 30px;
                        @include respond-to(lg) {
                            margin-top: 24px;
                        }
                    }
                }
                &__item {
                    @include font-size-get('p1');
                    color: $color-black;
                }
                &__item + #{$links}__item {
                    margin-top: 16px;
                }
            }
        }
    }
}
