.warning {
    &__inner {
        @include font-size-get('h3');
        color: $color-black;
        text-align: center;
        background-color: $color-yellow-light;
        transition: all ease-in-out 150ms;
        padding: 16px 12px;
        border-radius: 4px;
        font-weight: bold;
        &:hover {
            background-color: $color-yellow;
        }
    }

}
