.button {
    $button: &;

    &--fluid {
        width: 100%;
    }

    &--type {
        &-main {
            display: inline-flex;
            justify-content: center;
            align-items: center;
            color: $color-black;
            @include font-size-get('button');
            padding: 7px 16px;
            transition: all ease-in-out 150ms;
            text-decoration: none;
            border-radius: 4px;
            cursor: pointer;
            position: relative;
            font-weight: 500;
            border: 1px solid transparent;
            white-space: nowrap;

            @include respond-to(md) {
                width: 100%;
            }

            &-yellow {
                background-color: $color-yellow;
                border-color: $color-yellow;

                &:hover {
                    background-color: $color-yellow-light;
                    border-color: $color-yellow-light;
                }
            }

            &-green {
                background-color: $color-green;
                border-color: $color-green;

                &:hover {
                    background-color: $color-green-light;
                    border-color: $color-green-light;
                }
            }
        }

        &-secondary {
            display: inline-flex;
            justify-content: center;
            align-items: center;
            color: $color-black;
            @include font-size-get('button');
            padding: 7px 16px;
            transition: all ease-in-out 150ms;
            text-decoration: none;
            border-radius: 4px;
            border: 1px solid;
            cursor: pointer;
            position: relative;
            font-weight: 500;
            white-space: nowrap;

            @include respond-to(md) {
                width: 100%;
            }

            &-yellow {
                border-color: $color-yellow;
                background-color: transparent;

                &:hover {
                    border-color: $color-yellow-light;
                    background-color: $color-yellow-light;
                }
            }

            &-black {
                border-color: $color-black;
                background-color: transparent;

                &:hover {
                    background-color: $color-black;
                    color: $color-white;
                }
            }
        }
    }

    &__icon {
        $icon: &;
        position: absolute;
        transition: all ease-in-out 150ms;
        pointer-events: none;
        opacity: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        top: 50%;
        left: 50%;
        margin-top: -12px;
        margin-left: -12px;

        &-loading {
            margin-top: -2px;
        }

        &-svg {
            display: flex;
            justify-content: center;
            align-items: center;
            fill: $color-black;
        }
    }

    &--state {
        &-success#{$button}--type-main, &-success#{$button}--type-secondary, &-error#{$button}--type-main, &-error#{$button}--type-secondary, &-loading#{$button}--type-main, &-loading#{$button}--type-secondary {
            color: transparent;
            pointer-events: none;
        }

        &-success {
            #{$button}__icon-success {
                opacity: 1;
            }
        }

        &-error {
            #{$button}__icon-error {
                opacity: 1;
            }
        }

        &-loading {
            #{$button}__icon-loading {
                opacity: 1;
            }
        }
    }

    &--fluid {
        width: 100%;
        display: block;
        text-align: center;
    }
}
