.bebek {
    &__image {
        height: 100%;
        border-radius: 8px;
        overflow: hidden;
        position: relative;
        will-change: transform;
        min-height: 324px;

        @include respond-to(lg) {
            min-height: 228px;
        }

        &-item {
            $item: &;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            display: flex;
            z-index: 1;
            transition: transform ease-in-out 300ms;
            cursor: pointer;
            overflow: hidden;

            &-bg {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                display: flex;
                background-size: cover;
                background-position: center;
                transition: transform ease-in-out 300ms;
            }

            &:hover {
                #{$item}-bg {
                    transform: scale(1.05);
                }
            }

            &.next {
                z-index: 3;
                animation-duration: 500ms;
                animation-fill-mode: forwards;
                animation-name: object-image-item-next;


                &-reverse {
                    z-index: 3;
                    animation-duration: 500ms;
                    animation-fill-mode: forwards;
                    animation-name: object-image-item-next-reverse;
                }
            }

            &.active {
                z-index: 2;
            }

            @keyframes object-image-item-next {
                0% {
                    transform: translateX(100%);
                }
                100% {
                    transform: translateX(0);
                }
            }

            @keyframes object-image-item-next-reverse {
                0% {
                    transform: translateX(-100%);
                }
                100% {
                    transform: translateX(0);
                }
            }
        }
    }

    &__info {
        min-height: 100%;
        overflow: hidden;
        position: relative;
        transition: height ease-in-out 300ms;
        will-change: height;

        &-item {
            display: flex;
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            opacity: 0;
            transition: opacity ease-in-out 500ms;
            will-change: opacity;

            @include respond-from-to(md, lg) {
                column-count: 2;
            }

            &.active {
                opacity: 1;
                animation-duration: 500ms;
                animation-fill-mode: forwards;
                animation-name: bebel-info-item-active;

                @include respond-from-to(md, lg) {
                    display: block;
                }

                @keyframes bebel-info-item-active {
                    0% {
                        opacity: 0;
                    }
                    30% {
                        opacity: 0;
                    }
                    100% {
                        opacity: 1;
                    }
                }
            }

            flex-direction: column;
            align-items: flex-start;
            justify-content: flex-start;
        }

        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;

        &-title {
            @include font-size-get('h2');
            font-weight: 700;
            text-decoration: none;
            color: $color-black;
            transition: border-color ease-in-out 150ms;
            border-bottom: 2px solid;
            border-color: transparent;
            display: inline-block;

            &:hover {
                border-color: $color-yellow;
            }
        }

        &-statistic {
            @include font-size-get('p1');
            border-bottom: 1px solid $color-yellow;
            text-decoration: none;
            color: $color-black;
            display: inline-block;
        }

        &-tags {
            display: flex;
            flex-direction: column;

            &-item {
                & + & {
                    margin-top: 8px;
                }
            }
        }

        &-text {
            @include font-size-get('p1');
        }

        &-date, &-area {
            @include font-size-get('p1');
            color: $color-grey-dark;
        }

        &-title + &-statistic, &-statistic + &-tags, &-tags + &-text, &-text + &-date {
            margin-top: 16px;
        }

        &-date + &-area {
            margin-top: 8px;
        }
    }

    &__controls {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
        padding-top: 50px;
        opacity: 0;
        transition: opacity ease-in-out 300ms;
        will-change: opacity;

        @include respond-to(md) {
            padding-top: 0;
        }

        &.show {
            opacity: 1;
        }

        &-image {
            height: 156px;
            display: flex;
            width: 100%;
            position: relative;

            @include respond-to(md) {
                display: none;
            }

            &-item {
                $item: &;
                position: absolute;
                left: 0;
                top: 0;
                height: 100%;
                width: 100%;
                display: flex;
                overflow: hidden;
                border-radius: 8px;
                z-index: 1;
                background-color: $color-white;
                transform: scale(0.8) translateY(-55px);

                &.other {
                    z-index: 2;
                    transform: scale(0.8) translateY(-55px);
                }

                &.other-next {
                    animation-duration: 500ms;
                    animation-fill-mode: forwards;
                    animation-name: image-item-other-to-next;
                }

                &.other-next-reverse {
                    z-index: 2;
                    animation-duration: 500ms;
                    animation-fill-mode: forwards;
                    animation-name: image-item-other-to-next-reverse;
                }

                &.next {
                    z-index: 3;
                    transform: scale(.9) translateY(-25px);
                }

                &.next-active {
                    animation-duration: 500ms;
                    animation-fill-mode: forwards;
                    animation-name: image-item-next;
                }

                &.next-active-reverse {
                    animation-duration: 500ms;
                    animation-fill-mode: forwards;
                    animation-name: image-item-next-reverse;
                    z-index: 4;

                    #{$item}-img {
                        opacity: .4;
                    }
                }

                &.active {
                    z-index: 4;
                    opacity: 1;
                    transform: scale(1) translateY(0);

                    #{$item}-img {
                        opacity: 1;
                    }
                }

                &.active-hide {
                    animation-duration: 500ms;
                    animation-fill-mode: forwards;
                    animation-name: image-item-hide;
                    opacity: 1;

                    #{$item}-img {
                        opacity: .4;
                    }
                }

                &.active-hide-reverse {
                    z-index: 5;
                    animation-duration: 500ms;
                    animation-fill-mode: forwards;
                    animation-name: image-item-hide-reverse;
                }

                @keyframes image-item-hide {
                    0% {
                        opacity: 1;
                        pointer-events: none;
                        transform: scale(1) translateY(0) !important;
                    }
                    100% {
                        opacity: 0;
                        pointer-events: none;
                        transform: scale(1) translateY(0) !important;
                    }
                }

                @keyframes image-item-hide-reverse {
                    0% {
                        opacity: 0;
                        transform: scale(1) translateY(0);
                    }
                    100% {
                        opacity: 1;
                        transform: scale(1) translateY(0);
                    }
                }

                @keyframes image-item-next {
                    0% {
                        transform: scale(.9) translateY(-25px);
                    }
                    100% {
                        transform: scale(1) translateY(0);
                    }
                }

                @keyframes image-item-next-reverse {
                    0% {
                        transform: scale(1) translateY(0);
                    }
                    100% {
                        transform: scale(.9) translateY(-25px);
                    }
                }

                @keyframes image-item-other-to-next {
                    0% {
                        transform: scale(0.8) translateY(-55px);
                    }
                    100% {
                        transform: scale(.9) translateY(-25px);
                    }
                }

                @keyframes image-item-other-to-next-reverse {
                    0% {
                        transform: scale(.9) translateY(-25px);
                    }
                    100% {
                        transform: scale(0.8) translateY(-55px);
                    }
                }

                &-img {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    display: flex;
                    background-size: cover;
                    background-position: center;
                    transition: all ease-in-out 500ms;
                    cursor: pointer;
                    opacity: .4;

                    #{$item}.next-active & {
                        opacity: 1;
                    }

                    &:hover {
                        transform: scale(1.05);
                    }
                }
            }
        }

        &-title {
            display: block;
            height: 22px;
            position: relative;
            @include respond-to(md) {
                display: none;
            }

            &-item {
                display: flex;
                align-items: center;
                justify-content: space-between;
                position: absolute;
                left: 0;
                top: 0;
                height: 100%;
                width: 100%;
                opacity: 0;
                transition: opacity ease-in-out 300ms;
                will-change: opacity;

                &.active {
                    opacity: 1;
                }
            }
        }

        &-name {
            @include font-size-get('h3');
            font-weight: 700;
        }

        &-value {
            display: flex;
            text-decoration: none;
            color: $color-black;
            @include font-size-get('p1');
            border-bottom: 1px solid $color-yellow;
        }

        &-image + &-title {
            margin-top: 8px;
        }

        &-title + &-nav {
            margin-top: 32px;

            @include respond-to(lg) {
                margin-top: 16px;
            }
        }
    }

    &__nav {
        $nav: &;
        display: none;
        align-items: center;
        justify-content: center;

        &.show {
            display: flex;
        }

        &-prev, &-next {
            $control: &;
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: center;

            &-svg {
                fill: $color-grey-medium;
                opacity: .4;
                transition: opacity ease-in-out 150ms;
                will-change: opacity;

                #{$nav}-next & {
                    transform: rotate(180deg);
                }

                #{$nav}-prev:hover &, #{$nav}-next:hover & {
                    opacity: 1;
                }
            }
        }

        &-prev + &-next {
            margin-left: 8px;
        }
    }
}
