.row {
    &--around {
        margin: -12px -12px;

        & > [class^='col-'] {
            padding: 12px 12px;
        }

        &-10 {
            margin: -5px -12px;

            & > [class^='col-'] {
                padding: 5px 12px;
            }
        }

        &-16 {
            margin: -8px -12px;

            & > [class^='col-'] {
                padding: 8px 12px;
            }
        }

        &-20 {
            margin: -10px -12px;

            & > [class^='col-'] {
                padding: 10px 12px;
            }
        }

        &-24-16 {
            margin: -12px -12px;

            @include respond-to(md) {
                margin: -8px -8px;
            }

            & > [class^='col-'] {
                padding: 12px 12px;
                @include respond-to(md) {
                    padding: 8px 8px;
                }
            }
        }

        &-32 {
            margin: -16px -12px;

            & > [class^='col-'] {
                padding: 16px 12px;
            }
        }

        &-40 {
            margin: -20px -12px;
            @include respond-to(lg) {
                margin: -16px -12px;
            }

            & > [class^='col-'] {
                padding: 20px 12px;
                @include respond-to(lg) {
                    padding: 16px 12px;
                }
            }
        }
    }

    &--relative {
        position: relative;
    }
}

.container {
    &--fluid {
        height: 100%;
        width: 100%;
    }
}
