.section {
    &__title {
        @include font-size-get('h1');
        color: $color-black;
        font-weight: bold;
        display: flex;
        align-items: flex-end;

        @include respond-to(md) {
            flex-direction: column;
            align-items: flex-start;
        }

        &-text {

        }

        &-more {
            text-decoration: none;
            color: $color-grey-dark;
            @include font-size-get('p1');
            font-weight: 400;
            margin-bottom: 4px;
            border-bottom: 1px solid;
            border-color: transparent;
            transition: border-color ease-in-out 150ms;

            &:hover {
                border-color: $color-yellow;
            }


            @include respond-to(lg) {
                margin-bottom: 2px;
            }

            @include respond-to(md) {
                margin-bottom: 0;
            }
        }

        &-text + &-more {
            margin-left: 24px;

            @include respond-to(lg) {
                margin-left: 16px;
            }

            @include respond-to(md) {
                margin-left: 0;
                margin-top: 8px;
            }
        }
    }

    &__title + &__body {
        margin-top: 32px;
        @include respond-to(lg) {
            margin-top: 24px;
        }
    }

    &__title + &__description {
        margin-top: 32px;
        @include respond-to(lg) {
            margin-top: 24px;
        }
    }

    &__description {
        @include font-size-get('p1');
        color: $color-black;
    }

    &__description + &__body {
        margin-top: 32px;
        @include respond-to(lg) {
            margin-top: 24px;
        }
    }

    &__body {
        &-name {
            @include font-size-get('h2');
            color: $color-black;
            font-weight: bold;
        }

        &-name + &-block {
            margin-top: 16px;
        }
    }

    & + & {
        margin-top: 72px;
        @include respond-to(lg) {
            margin-top: 48px;
        }
    }

    & + &--concat-small {
        margin-top: 32px;
    }

    &__body + &__footer {
        margin-top: 16px;
    }

    &__footer {

    }
}
