.page {
    $page: &;
    display: flex;
    flex-direction: column;
    transition: opacity ease-in-out 300ms;
    will-change: opacity;

    &__top {
        flex: 1 0 auto;
        position: relative;
        z-index: 1000;

        @include respond-to(lg) {
            padding-top: 66px;
        }

        @include respond-to(md) {
            padding-top: 51px;
        }
    }

    &__bottom {

    }

    &__top, &__bottom {
        opacity: 0;
        will-change: opacity;
        transition: opacity ease-in-out 300ms;

        #{$page}.active & {
            opacity: 1 !important;
        }
    }

    &__inner {
        padding: 0 0 72px;

        @include respond-to(lg) {
            padding: 0 0 48px;
        }
    }

    /*&__inner {
        padding: 40px 0 72px;

        @include respond-to(lg) {
            padding: 32px 0 48px;
        }
    }*/
}
