// Common styles of the project

html, body {
    height: 100%;
    font-family: $font-family-base;
    cursor: default;
    @include font-size-get('p1');
}

.page {

}

.page__wrapper {
    min-height: 100%;
    margin-bottom: -50px;
}

* html .page__wrapper {
    height: 100%;
}

.page__buffer {
    height: 50px;
}

h1 {
    @include font-size-get('h1');
    font-weight: 700;
}

h2 {
    @include font-size-get('h2');
    font-weight: 700;
}

h3 {
    @include font-size-get('h3');
    font-weight: 700;
}

p {
    @include font-size-get('p1');
    font-weight: 400;
}

.img {
    &-fluid {
        width: 100%;
    }

    &-bordered {
        border-radius: 8px;
    }

    &--fluid {
        max-width: 100%;
    }

    &--bordered {
        border-radius: 8px;
    }
}

.text {
    &--small {
        @include font-size-get('p2');
        font-weight: 400;
    }
}

.pointer {
    &-none {
        pointer-events: none;
    }
}
