.pagination {
    display: flex;
    align-items: center;
    $pagination: &;

    &__item {
        @include font-size-get('p1');
        color: $color-black;
        text-decoration: none;
        height: 36px;
        width: 40px;
        background-color: $color-grey-light;
        border-radius: 2px;
        cursor: pointer;
        transition: all ease-in-out 150ms;
        display: flex;
        align-items: center;
        justify-content: center;

        @include respond-to(md) {
            height: 34px;
            width: 36px;
        }

        & + & {
            margin-left: 8px;
        }

        &:hover {
            background-color: $color-yellow-light;
        }

        &.active {
            pointer-events: none;
            background-color: $color-yellow;
        }

        &--more {
            height: 16px;
            width: 16px;
            background-color: transparent !important;
        }
    }

    &__more {
        display: flex;
        align-items: center;

        &-item {
            width: 4px;
            height: 4px;
            background-color: $color-grey-light;
            border-radius: 2px;
            transition: all ease-in-out 150ms;

            & + & {
                margin-left: 2px;
            }

            #{$pagination}__item:hover & {
                background-color: $color-yellow-light;
            }
        }
    }
    &--center {
        justify-content: center;
    }
}
