.tag {
    $tag: &;
    padding: 6px 12px;
    @include font-size-get('p1');
    border-radius: 16px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    cursor: auto;
    transition: all ease-in-out 150ms;
    text-decoration: none;
    color: $color-black;
    font-weight: 400;

    &--color {
        &-grey {
            background-color: $color-grey-light;
        }

        &-yellow {
            background-color: $color-yellow;
        }

        &-white {
            background-color: $color-white;
        }
    }
    &--hover {
        cursor: pointer;
        &:hover {
            background-color: $color-yellow;
        }
    }


    &__icon {
        display: flex;
        align-items: center;
        justify-content: center;

        &-svg {
            fill: $color-black;
        }
    }

}
