.uikit {
    &__section {
        &-header {
            color: $color-green;
            margin-bottom: 20px;
            @include font-size-get('h1');
            font-weight: 700;
        }

        & + & {
            margin-top: 50px;
        }

        &--forms {
            & > .uikit__row + .uikit__row{
                margin-top: 25px;
                padding-top: 25px;
                border-top: 2px solid $color-gray;
            }
        }
    }

    &__row {
        margin: -5px -10px;
        display: flex;
        align-items: center;
        flex-wrap: wrap;

        &--no-margin {
            margin: 0;
        }

        & > .row {
            min-width: 100%;
        }

        & + & {
            margin-top: 20px;
        }

        &-item {
            height: 100%;
            display: flex;
            align-items: flex-start;
            @include respond-to(md) {
                width: 100%;
            }

            &--grey {
                padding: 32px;
                border-radius: 8px;
                background-color: $color-grey-light;
            }
        }

        & > &-item {
            margin: 5px 10px;
        }
    }

    &__block {
        margin: 20px 0;

        &--yellow {
            padding: 32px;
            background-color: $color-yellow;
            border-radius: 8px;
        }
    }
}
