.modal {
    $modal: &;

    &-dialog {
        @include respond-to(md) {
            margin: 0 auto;
            align-items: start;
        }
    }

    &-content {
        border: none;
        border-radius: 8px;
        overflow: hidden;
        @include respond-to(md) {
            border-radius: 0;
        }
    }

    &--type {
        &-main {
            #{$modal} {
                &-dialog {
                    max-width: 424px;
                    @include respond-to(lg) {
                        max-width: 336px;
                    }
                    @include respond-to(md) {
                        max-width: 100%;
                    }
                }

                &__close {
                    position: absolute;
                    top: 8px;
                    right: 8px;
                    opacity: .4;
                    transition: all ease-in-out 150ms;
                    cursor: pointer;
                    z-index: 1;
                    &-svg {
                        fill: $color-black;
                    }

                    &:hover {
                        opacity: 1;
                    }
                }

                &__image {

                    height: 212px;
                    position: relative;

                    @include respond-to(lg) {
                        height: 152px;
                    }
                    &-layer {
                        position: absolute;
                        left: 8px;
                        right: 8px;
                        top: 0;
                        background-size: contain;
                        background-position: center;
                        background-repeat: no-repeat;
                        height: 100%;
                        @include respond-to(lg) {
                            left: 22px;
                            right: 22px;
                        }
                        @include respond-to(md) {
                            left: 14px;
                            right: 14px;
                        }
                    }
                    &--color {
                        &-sand {
                            background-color: $color-bg-sand;
                        }

                        &-red-light {
                            background-color: $color-bg-red-light;
                        }

                        &-grape {
                            background-color: $color-bg-grape;
                        }

                        &-blue-light {
                            background-color: $color-bg-blue-light;
                        }

                        &-green-light {
                            background-color: $color-bg-green-light;
                        }
                    }

                    &--size {
                        &-small {
                            height: 104px;
                            background-size: auto;
                            background-position: center bottom;
                            background-repeat: no-repeat;
                        }
                    }
                }

                &__description {
                    padding: 24px 16px 16px 16px;
                }

                &__title {
                    @include font-size-get('h2');
                    color: $color-black;
                    font-weight: bold;
                }

                &__title + #{$modal}__form,
                &__title + #{$modal}__object,
                &__object + #{$modal}__form {
                    margin-top: 16px;
                }
                &__object + #{$modal}__form--concat {
                    &-large {
                        margin-top: 24px;
                    }
                }
                &__object + #{$modal}__title {
                    margin-top: 24px;
                }

                &__object {
                    &-name {
                        @include font-size-get('h3');
                        color: $color-black;
                        font-weight: bold;
                    }

                    &-name + #{$modal}__object-properties {
                        margin-top: 8px;
                    }

                    &-description + #{$modal}__properties {
                        margin-top: 16px;
                    }

                    &-description {
                        @include font-size-get('p1');
                        color: $color-black;
                    }

                    &-properties {
                        display: flex;
                    }

                    &-property + #{$modal}__object-property {
                        margin-left: 8px;
                    }

                    &-property {
                        @include font-size-get('p1');
                        color: $color-black;
                    }
                }
                &__properties {
                    display: flex;
                    padding-bottom: 24px;
                    border-bottom: 2px solid $color-grey-light;
                }
                &__property {
                    &-name {
                        @include font-size-get('p1');
                        color: $color-grey-dark;
                    }
                    &-name + #{$modal}__property-value {
                        margin-top: 8px;
                    }
                    &-value {
                        @include font-size-get('p1');
                        color: $color-black;
                    }
                    & + #{$modal}__property {
                        margin-left: 32px;
                    }
                }
            }
        }

        &-video {
            position: fixed;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            overflow: hidden;
            background-color: $color-rgba-white-90;

            #{$modal} {
                &-dialog {

                    max-width: 900px;
                    @include respond-to(xl) {
                        max-width: 768px;
                    }
                    @include respond-to(lg) {
                        max-width: 100%;
                    }
                }

                &-content {
                    border: none;
                    border-radius: 0;
                    overflow: visible;
                }

                &__video {
                    position: relative;
                    display: block;
                    width: 100%;
                    padding: 0;
                    overflow: hidden;

                    &:before {
                        display: block;
                        content: "";
                        padding-top: 56.25%;
                    }

                    &-item {
                        position: absolute;
                        top: 0;
                        bottom: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                        border: 0;
                    }
                }

                &__close {
                    position: absolute;
                    background-color: $color-red;
                    width: 32px;
                    height: 32px;
                    border-radius: 100%;
                    right: -16px;
                    top: -16px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    z-index: 10;
                    cursor: pointer;
                    transition: all ease-in-out 150ms;
                    @include respond-to(lg) {
                        top: -96px;
                        left: 50%;
                        width: 48px;
                        height: 48px;
                        transform: translateX(-50%);
                    }

                    &-svg {
                        fill: $color-white;
                        @include respond-to(lg) {
                            width: 42px;
                            height: 42px;
                        }
                    }

                    &:hover {
                        transform: scale(1.1);
                        @include respond-to(lg) {
                            transform: translateX(-50%) scale(1.1);
                        }
                    }
                }
            }
        }
    }

}
