.header {
    background-color: $color-white;
    position: relative;
    border-bottom: 1px solid $color-grey-light;
    box-sizing: border-box;

    @include respond-to(lg) {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 1000;
    }

    &__top {
        background-color: $color-white;
        position: relative;
        z-index: 1000;
    }

    &__bottom {
        position: relative;
        z-index: 999;
    }

    .header__inner {
        padding: 22px 0 21px;
        display: flex;
        align-items: center;

        @include respond-to(lg) {
            padding: 16px 0 15px;
        }

        @include respond-to(md) {
            padding: 9px 0;
        }
    }

    &__logo {
        display: flex;
        flex: 1 0 auto;
        font-size: 14px;
        line-height: 19px;
        letter-spacing: 0.2em;
        text-transform: uppercase;
        font-weight: 800;
        text-decoration: none;
        justify-content: flex-start;
        color: $color-black;
        height: 40px;
        @include respond-to(lg) {
            height: 32px;
        }

        &-link {
            margin-left: -3px;
            display: flex;
            justify-content: center;
            align-items: center;
            text-decoration: none;
            color: $color-black;
            @include font-size-get('h2');
        }

        &-svg {
            height: 40px;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            width: auto;

            @include respond-to(lg) {
                height: 32px;
            }
        }
    }

    &__content {
        display: flex;
        align-items: center;
    }

    &__info {
        display: flex;
        align-items: center;

        &-item {
            & + & {
                margin-left: 16px;
            }

            &--phone {
                @include font-size-get('h3');
                font-weight: 700;
            }
        }
    }

    &__links {
        display: flex;
        align-items: center;

        &-item {
            & + & {
                margin-left: 32px;
            }
        }
    }

    &__info + &__links, &__info + &__burger, &__links + &__burger {
        margin-left: 32px;
    }
}
