.burger {
    $burger: &;
    display: flex;
    align-items: center;
    cursor: pointer;

    &__text {
        padding-top: 1px;
        border-bottom: 1px solid;
        border-color: transparent;
        transition: border-color ease-in-out 150ms;

        #{$burger}:hover & {
            border-color: $color-yellow;
        }
    }

    &__text + &__dots {
        margin-left: 8px;
    }

    &__dots {
        display: flex;
        align-items: center;

        &-item {
            & + & {
                margin-left: 2px;
            }

            width: 4px;
            height: 4px;
            border-radius: 2px;
            background-color: $color-yellow;
        }
    }
}
