.banner {
    width: 100%;
    overflow: hidden;
    border-radius: 8px;

    &--color {
        &-blue-light {
            background-color: $color-bg-blue-light;
        }
        &-sand {
            background-color: $color-bg-sand;
        }
        &-green-light {
            background-color: $color-bg-green-light;
        }
        &-red-light {
            background-color: $color-bg-red-light;
        }
        &-grape {
            background-color: $color-bg-grape;
        }

    }

    &__inner {
        width: 100%;
    }

    &__info {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 32px 24px;
        height: 100%;
        min-height: 212px;

        @include respond-to(lg) {
            min-height: 174px;
        }

        @include respond-to(lg) {
            padding: 24px 0 24px 24px;
        }
        @include respond-to(md) {
            padding: 16px 12px;
        }
    }

    &__title {
        @include font-size-get('h1');
        font-weight: 700;
    }

    &__text {
        @include font-size-get('p1');
    }

    &__title + &__text {
        margin-top: 8px;
    }

    &__title + &__actions, &__text + &__actions {
        margin-top: 16px;
    }

    &__actions {
        flex: 1 0 auto;
        display: flex;
        align-items: flex-end;
        margin: -8px;

        @include respond-to(md) {
            flex-direction: column;
            width: 100%;
            margin: 0;
        }

        &-item {
            padding: 8px;

            @include respond-to(md) {
                width: 100%;
                padding: 0;
            }
            & + & {
                @include respond-to(md) {
                    margin-top: 16px;
                }
            }
        }
    }

    &__image {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: flex-end;

        &-img {
            max-width: 100%;
        }
    }
}
